@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movieRow {
    margin-bottom: 30px;
}

.movieRow h2 {
    margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list {
    transition: all ease 0.5s;
}

.movieRow--item {
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img {
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all ease 0.2s
}

.movieRow--left,
.movieRow--right {
    position: absolute;
    width: 40px;
    height: 225px;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.6);
    cursor: pointer;
    opacity: 0;
}

.movieRow--left {
    left: 0;
}

.movieRow--right {
    right: 0;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right {
    opacity: 1;
    transition: all ease 0.5s;
}
@media(max-width:760px) {

    .movieRow--left,
    .movieRow--right {
        opacity: 1;
    }

    .movieRow h2 {
        font-size: 18px;
    }
}
.featured {
    height: 100vh;
}

.feature--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.feature--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured--name {
    font-size: 50px;
    font-weight: bold;
}

.featured--info {
    font-size: 16px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--year,
.featured--seasons,
.featured--points {
    display: inline-block;
    margin-right: 15px;
}

.featured--points {
    color: #46d369;
}

.featured--description {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
    max-width: 40%;
}

.featured--btn {
    margin-top: 15px;
}

.featured--watch,
.featured--list {
    display: inline-block;
    font-size: 18px;
    font-weight: bold;
    padding: 10px 25px;
    border-radius: 5px;
    margin-right: 10px;
    opacity: 1;
    border: none;
    cursor: pointer;
}

.featured--watch:hover,
.featured--list:hover {
    opacity: 0.7;
    transition: all ease 0.2s
}

.featured--watch {
    background-color: #fff;
    color: #000;
}

.featured--list {
    background-color: #333;
    color: #fff;
}

.featured--genres {
    margin-top: 15px;
    font-size: 16px;
    color: #999
}
@media(max-width: 768px) {
    .featured {
        height: 90vh;
    }

    .featured--name {
        font-size: 36px;
    }

    .featured--info {
        font-size: 14px;
    }

    .featured--description {
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }

    .featured--watch,
    .featured--list {
        font-size: 14px
    }

    .featured--genres {
        font-size: 14px;
    }
}

@media(max-width: 300px) {

    .featured--watch,
    .featured--list {
        font-size: 12px
    }
}
header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
}

header.black {
    background-color: #141414;
    transition: all ease 0.5s;
}

.header--logo {
    height: 25px;
}

.header--logo img {
    height: 100%;
}

.header--user {
    height: 35px;
}

.header--user img {
    height: 100%;
    border-radius: 3px;
    cursor: pointer;
}
* {
    box-sizing: border-box;
}

body {
    background-color: #111;
    color: #fff;
    margin: 0;
    font-family: 'Roboto';
}

.lists {
    margin-top: -130px;
}

.loading {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading img {
    width: 600px;
    margin-right: 40px
}

footer {
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 0px;
    width: 100%;
    margin: 0;
    bottom: 0%;
    font-size: 18px;
    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
}

footer a {
    text-decoration: none;
    color: #d4d4d4;
}

footer a:hover {
    text-decoration: none;
    color: #a8e6c4;
    font-family: Arial, Helvetica, sans-serif;
}

@media(max-width: 768px) {
    footer {
        font-size: 0.8rem;
    }

    .loading img {
        margin-left: 10px;
    }
}
