.featured {
    height: 100vh;
}

.feature--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.feature--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured--name {
    font-size: 50px;
    font-weight: bold;
}

.featured--info {
    font-size: 16px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--year,
.featured--seasons,
.featured--points {
    display: inline-block;
    margin-right: 15px;
}

.featured--points {
    color: #46d369;
}

.featured--description {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
    max-width: 40%;
}

.featured--btn {
    margin-top: 15px;
}

.featured--watch,
.featured--list {
    display: inline-block;
    font-size: 18px;
    font-weight: bold;
    padding: 10px 25px;
    border-radius: 5px;
    margin-right: 10px;
    opacity: 1;
    border: none;
    cursor: pointer;
}

.featured--watch:hover,
.featured--list:hover {
    opacity: 0.7;
    transition: all ease 0.2s
}

.featured--watch {
    background-color: #fff;
    color: #000;
}

.featured--list {
    background-color: #333;
    color: #fff;
}

.featured--genres {
    margin-top: 15px;
    font-size: 16px;
    color: #999
}